import { state } from "cerebral/tags";
import { set } from "cerebral/operators";
import {
  //handleFieldCleared,
  validateNewFieldButton
} from "../fields/sequences";
import { setFieldOnMapCenter } from "../MapPanel/chains";
import { handleDrawerOpen as handleBottomDrawerOpen } from "../BottomDrawer/chains";

export var addOperationClicked = [
  setDisabledNewOperation,
  set(state`BottomDrawer.menu`, "operations"),
  set(state`BottomDrawer.open`, true)
];

export var operationInputTyped = [
  setDisabledNewOperation,
  operationInputTypedAction
];

export var fieldInputTyped = [
  fieldInputTypedAction
  //...handleFieldCleared
];

export var handleFieldEdited = [
  handleFieldEditedAction,
  ...handleBottomDrawerOpen,
  ...setFieldOnMapCenter
];

export var handleOperationEdited = [handleOperationEditedAction];

export var openSuggestions = [openSuggestionsAction];

export var closeSuggestions = [closeSuggestionsAction];

function openSuggestionsAction({ props, state }) {
  if (props.id === "ops-input") {
    state.set("User.currentSelection.operation.suggestionsOpen", true);
  } else {
    state.set("fields.new_field.field.suggestionsOpen", true);
  }
}

function closeSuggestionsAction({ props, state }) {
  if (props.id === "ops-input") {
    let currentOperationID = state.get("User.currentSelection.operation.id");
    if (currentOperationID) {
      // state.set(
      //   "User.currentSelection.operation.inputText",
      //   state.get(`${"operations.records." + currentOperationID + ".label"}`)
      // );
    }
    state.set("User.currentSelection.operation.suggestionsOpen", false);
  } else {
    state.set("fields.new_field.field.suggestionsOpen", false);
  }
}

/**
 * Sets the value of the property disabled according to
 * the value of the operation inputText
 * if already exists then disabled will be true (button cannot be clicked)
 * TODO: change this to a hashtable O(1) instead of O(n)
 * @param props
 * @param state
 */
function setDisabledNewOperation({ props, state }) {
  /* Disable add button if current operation exists in the state tree */
  /* fix this with a hash table O(1), the following procedure is inefficient */
  let _inputText = props.inputText;
  let _operations = state.get("operations.records");
  let openBottonDrawer = state.get("BottomDrawer.open");
  // if the bottomDrawer is still not open then use the following value
  // state.get('User.currentSelection.operation.inputText');
  if (!openBottonDrawer) {
    _inputText = state.get("User.currentSelection.operation.inputText");
  }
  let _found = false;
  Object.keys(_operations || {}).forEach(function(key) {
    //console.log(key, _operations[key]);
    if (_inputText === _operations[key].label || _inputText.length === 0) {
      state.set("User.currentSelection.operation.disabled", true);
      _found = true;
    } else {
      if (!_found) {
        state.set("User.currentSelection.operation.disabled", false);
      }
    }
  });
}

