function operationInputTypedAction({ props, state }) {
  state.set("User.currentSelection.operation.inputText", props.inputText);
  state.set("User.currentSelection.operation.suggestionsOpen", true);
}

function fieldInputTypedAction({ props, state }) {
  let id = state.get(`fields.selectedId`);
  state.set(`fields.records.${id}.name`, props.inputText);
  state.set("fields.new_field.field.suggestionsOpen", true);
}

function handleFieldEditedAction({ props, state }) {
  let id = state.get(`fields.selectedId`);
  let boundary = JSON.parse(
    JSON.stringify(state.get(`fields.records.${id}.boundary`))
  );
  let record = JSON.parse(
    JSON.stringify(state.get(`fields.records.${id}`))
  );
  console.log("--> copying this boundary into buffer", boundary);
  if (boundary && boundary.geojson.coordinates[0].length >= 3) {
    state.set("User.currentSelection.field.coordinatesOk", true);
  } else {
    state.set("User.currentSelection.field.coordinatesOk", false);
  }
  state.set("fields.new_field", record);

  validateNewFieldButton({ props, state });

  state.set("fields.temp_buffer.boundary", boundary);
  state.set("fields.editing", true);
  state.set("fields.isNewField", false);
}

function handleOperationEditedAction({ props, state }) {
  state.set("User.currentSelection.operation.editing", true);
}
