import { toggle, set } from "cerebral/operators";
import { state } from "cerebral/tags";

export var handleDrawerOpen = [toggle(state`BottomDrawer.open`)];

export var openBottomDrawerForStatusUpdate = [
  set(state`BottomDrawer.menu`, "status-update"),
  set(state`BottomDrawer.open`, true)
];

export var handleOperatorMenuOpen = [
  set(state`BottomDrawer.operatorMenuOpen`, true)
];

export var handleOperatorMenuClose = [
  set(state`BottomDrawer.operatorMenuOpen`, false)
];
