import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import { connect } from '@cerebral/react';
import {state, signal} from 'cerebral/tags';
import ListSubheader from 'material-ui/List/ListSubheader';
import List, { ListItem, ListItemText } from 'material-ui/List';
import Collapse from 'material-ui/transitions/Collapse';
import ExpandLess from 'material-ui-icons/ExpandLess';
import ExpandMore from 'material-ui-icons/ExpandMore';
import Typography from 'material-ui/Typography';

const styles = theme => ({
  root: {
    maxWidth: 310, //was 360
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing.unit * 4,
  },
});

class OpsList extends React.Component {

  renderItem(params) {
    const {key, color1, color2, bannerText, classes} = params;
    const area = this.props.OpsList[key]['fields'].reduce((a, b) => {return a + (b.boundary ? b.boundary.area : 0)}, 0);
    const percent = !(!params['total_area']) ?
                      Math.round(( area / params['total_area'] + 0.00001) * 10000) / 100 : 0;
    return(
      <div key={'fields-' + key}>
        <ListItem
          button
          style={{color: color1}}
          onClick={ () => {this.props.handleCollapse({status: key})} }
        >
          <ListItemText
            primary={
              <Typography variant="body2" style={{color: color1}}>
                {bannerText + ' (' + this.props.OpsList[key]['fields'].length + ')'}
              </Typography>
            }
            secondary={
              <Typography variant="caption" style={{color: color2}}>
                {"" + (Math.round((area + 0.00001) * 100) / 100) + ((percent > 0) ? ' acres (' + percent + '%)' : ' acres')}
              </Typography>
            }
          />
          {this.props.OpsList[key]['fields'].length > 0 ? (
            !this.props.OpsList[key]['collapse'] ?
              <ExpandLess /> :
              <ExpandMore />
            ) : null }
        </ListItem>
        <Collapse in={!(this.props.OpsList[key]['collapse'])} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {this.props.OpsList[key]['fields'].map(fieldRecord => {
              return (
                <ListItem
                  button style={{color: color1}}
                  className={classes.nested}
                  key={'field-' + fieldRecord.id}
                  onClick={() => {}}
                >
                  <ListItemText
                    disableTypography
                    primary={
                      <Typography variant="body2" style={{color: color1}} >
                        {fieldRecord.name + ' (' + (Math.round((fieldRecord.boundary.area + 0.00001) * 100) / 100) + ' acres)'}
                      </Typography>}

                    secondary={
                      <div>
                        <Typography variant="caption" style={{color: color2}}>
                          Since: {fieldRecord.since}
                        </Typography>
                        <Typography variant="caption" style={{color: color2}}>
                          Marked By: {fieldRecord.by}
                        </Typography>
                      </div>}
                  />
                </ListItem>
              )
            })}
          </List>
        </Collapse>
      </div>
    )
  }

  render() {
    const { classes } = this.props;//theme
    const params = {
      unplanned: {
        color1: '#263238',
        bannerText: 'Unplanned',
        color2: '#B0BEC5',
      },
      planned: {
        color1: '#D50000',
        bannerText: 'Planned',
        color2: '#EF9A9A',
      },
      started: {
        color1: '#FFC107',
        bannerText: 'Started',
        color2: '#FFC107',
      },
      completed: {
        color1: '#1B5E20',
        bannerText: 'Completed',
        color2: '#66BB6A',
      },
      total_area: Object.keys(this.props.OpsList || {}).map(
                    key => {
                      return this.props.OpsList[key]['fields'].reduce((a, b) => {
                        return a + (b.boundary ? b.boundary.area : 0)}, 0)
                    }).reduce((a, b) => {
                          return a + b}, 0)
    };

    return (
        <List
          component="nav"
          className={classes.root}
          subheader={
            <ListSubheader component="div" style={{color: '#304FFE', display: 'flex', alignItems: 'center'}}>
              <Typography variant="caption">
                {
                  Object.keys(this.props.OpsList || {}).map(key => {
                    return this.props.OpsList[key]['fields'].length}).reduce((a, b) => {
                      return a + b}, 0) +
                  ' fields, ' + (Math.round((params['total_area'] + 0.00001) * 100) / 100) + ' acres '
                }
              </Typography>
            </ListSubheader>}
        >
            {Object.keys(this.props.OpsList || {}).map(key => {
              return this.renderItem({
                key: key,
                classes: classes,
                total_area: params['total_area'],
                ...params[key],
              })
            })}
        </List>
    );
  }
}

OpsList.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect({
    currentOperationName: state`operations.records.${state`User.currentSelection.operation.id`}.label`,
    OpsList: state`OpsList`,

    handleCollapse: signal`OpsList.handleCollapse`,
    selectField: signal`User.handleFieldSelected`
  },
  withStyles(styles, { withTheme: true })(OpsList)
);

