import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from '@cerebral/react';
import {signal, state} from 'cerebral/tags';

const styles = theme => ({
  hidden: {
    display: "none"
  },
  flexContainer: {
    display: 'flex',
    justifyContent: 'space-evenly',
    backgroundColor: '#333333',
    color: theme.palette.common.white,
    order: 2
  },
  pill: {
    textAlign: 'center',
    padding: '5px',
    marginTop: '5px',
    marginBottom: '5px',
    alignItems: 'center',
    borderRadius: '3px',
    color: '#FFFFFF',
    textShadow: '1px 1px #000000',
  },
  completed: {
    backgroundColor: '#66BB6A',
  },
  started: {
    backgroundColor: '#F0C000',
  },
  planned: {
    backgroundColor: '#CC0000',
  }
});

class StatsBar extends React.Component {

  render() {
    const { classes } = this.props;
    const props = this.props;

    return (
      <div className={!this.props.OpsListCompletedArea ? classes.hidden: classes.flexContainer}>
        <div className={`${classes.pill} ${classes.completed}`}>
          { (this.props.isSmall ? '' : "Completed " ) +
          Math.round(props.OpsListCompletedArea) + " ac " +
          ('(' + Math.round(props.OpsListCompletedPercentage) + "%)")
          }
        </div>

        <div className={`${classes.pill} ${classes.started}`}>
          { (this.props.isSmall ? '' : "Started ") +
          Math.round(props.OpsListStartedArea) + " ac " +
          ('(' + Math.round(props.OpsListStartedPercentage) + "%)" )
          }
        </div>

        <div className={`${classes.pill} ${classes.planned}`}>
          { (this.props.isSmall ? '' : "Planned ") +
          Math.round(props.OpsListPlannedArea) + " ac " +
          ('(' + Math.round(props.OpsListPlannedPercentage) + "%)")
          }
        </div>
      </div>
    );

  }//render
}

StatsBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect({
  appIsMobile:                  state`App.is_mobile`,
  orientation:                  state`window.orientation`,
  isSmall:                      state`window.isSmall`,
  connection_id:                state`operations.connection_id`,
  OpsListCompletedArea:         state`OpsList.completed.area`,
  OpsListCompletedPercentage:   state`OpsList.completed.percentage`,
  OpsListCompletedTotalFields:  state`OpsList.completed.totalFields`,
  OpsListStartedArea:           state`OpsList.started.area`,
  OpsListStartedPercentage:     state`OpsList.started.percentage`,
  OpsListStartedTotalFields:    state`OpsList.started.totalFields`,
  OpsListPlannedArea:           state`OpsList.planned.area`,
  OpsListPlannedPercentage:     state`OpsList.planned.percentage`,
  OpsListPlannedTotalFields:    state`OpsList.planned.totalFields`,

  handleUpdateAction:           signal`StatsBar.handleUpdateAction`,
  handleCloseCompleted:         signal`StatsBar.handleCloseCompleted`,
  handleCloseStarted:           signal`StatsBar.handleCloseStarted`,
  handleClosePlanned:           signal`StatsBar.handleClosePlanned`
},
withStyles(styles, { withTheme: true })(StatsBar)
);