import { sequence } from "cerebral";
import { set, when, equals } from "cerebral/operators";
import { state, props } from "cerebral/tags";
import Promise from "bluebird";
import oada from "@oada/cerebral-module/sequences";
import { loadData as loadOpsLstData } from "../OpsList/chains";
import { statsBarHandleUpdate } from "../StatsBar/chains";

//import {putFarm} from "../fields/sequences";
/* Configuration values */
let _localPath = "/bookmarks/operations";
let _TYPE = "application/vnd.oada.yield.1+json";
/* Tree that resembles the data model for operations module */
let tree = {
  bookmarks: {
    _type: "application/vnd.oada.bookmarks.1+json",
    _rev: "0-0",
    operations: {
      _type: "application/vnd.oada.yield.1+json",
      _rev: "0-0",
      "*": {
        _type: "application/vnd.oada.yield.1+json",
        _rev: "0-0"
      }
    }
  }
};

/**
 * fetches the operation
 * @type {Primitive}
 */
export const fetch = sequence("operations.fetch", [
  ({ state, props }) => ({
    connection_id: state.get("operations.connection_id"),
    path: _localPath,
    tree,
    watch: { signals: ["operations.handleWatchUpdate"] }
  }),
  buildFetchRequest,
  oada.get,
  when(state`oada.${props`connection_id`}.bookmarks.operations`),
  {
    true: sequence("fetchOperationsSuccess", [
      mapOadaToOperations,
      mapOperations,
      loadOpsLstData,
      statsBarHandleUpdate,
      set(state`App.emptyDataSet`, false),
    ]),
    false: sequence("fetchOperationsEmptySet", [
      () => (
        console.log("--> Operations empty set")
      ),
      set(state`App.emptyDataSet`, true),
    ]),

  }
]);

/**
 * builds requests in the expected format in the cerebral-module
 * @param state
 * @returns {{requests: *[]}}
 */
function buildFetchRequest({ state }) {
  let requests = [
    {
      connection_id: state.get("operations.connection_id"),
      path: _localPath,
      tree,
      watch: { signals: ["operations.handleWatchUpdate"] }
    }
  ];
  return { requests };
}

export const fetchNoWatch = sequence("operations.fetchNoWatch", [
  ({ state, props }) => ({
    connection_id: state.get("operations.connection_id"),
    path: _localPath,
    tree
  }),
  //oada.get,
  when(state`oada.${props`connection_id`}.bookmarks.operations`),
  {
    true: sequence("fetchOperationsSuccess", [
      mapOadaToOperations,
      mapOperations,
      loadOpsLstData,
      statsBarHandleUpdate,
      set(state`App.emptyDataSet`, false),
    ]),
    false: sequence("fetchOperationsEmptySetNoWatch", [
      () => (
        console.log("--> Operations empty set no watch")
      ),
      set(state`App.emptyDataSet`, true),
    ])
  }
]);

/**
 * requests the operation creation to the OADA module
 * @type {*[]}
 */
export const addBookmarksOperations = sequence(
  "operations.addBookmarksOperations",
  [buildBookmarksOperationsRequest, oada.put, fetch]
);

/**
 * Initialize the operations module
 * @type {Primitive}
 */
export const init = sequence("operations.init", [
  getConnectionsFromStorage,
  when(state`Connections.connection_id`),
  {
    true: [oada.connect],
    false: [
      oada.connect,
      set(state`operations.connection_id`, props`connection_id`),
      set(state`Connections.connection_id`, props`connection_id`),
      set(state`connections.operations.connection_id`, props`connection_id`)
    ]
  },
  //
  set(state`operations.loading`, true),
  fetch,
  when(state`oada.${props`connection_id`}.bookmarks.operations`),
  {
    true: [],
    false: [
      //addBookmarksOperations,
      //set(state`InfoBar.operations.open`, true)
    ]
  },
  set(state`operations.loading`, false),
  set(props`type`, "operations")
]);

/**
 * gets the Connections from localCache, it updates all necessary state to auto-init the app
 * @param state
 */
function getConnectionsFromStorage({ state }) {
  let connections = JSON.parse(
    localStorage.getItem("_FieldWorkApp.Connections")
  );
  console.log("--> operations.init storage connections --> ", connections);
  if (connections && connections["connection_id"]) {
    state.set("Connections.show", false);
    let connectionId = connections["connection_id"];
    let oadaDomain = connections["oada_domain"];
    console.log(
      "--> operations init connectionId and OadaDomain",
      connectionId,
      oadaDomain
    );
    state.set(`operations.connection_id`, connectionId);
    state.set("Connections.oada_domain", oadaDomain);
    state.set("Connections.oada_domain_text", oadaDomain);
  }
}

/**
 * refresh the operations module
 * when handling updates/watches
 * @type {Primitive}
 */
export const refresh = sequence("operations.refresh", [
  set(state`operations.connection_id`, props`connection_id`),
  set(state`operations.loading`, true),
  fetchNoWatch,
  set(state`operations.loading`, false),
  set(props`type`, "operations")
]);

/**
 * Handles updates in the resource
 * @type {*[]}
 */
export const handleWatchUpdate = sequence("operations.handleWatchUpdate", [
  () => {console.log("-->operations.handleWatchUpdate");},
  refresh,
  statsBarHandleUpdate
]);

export const selectOperation = [];

/**
 * Maps operations records
 * @param props
 * @param state
 * @returns {*}
 */
export function mapOadaToOperations({ props, state }) {
  let connection_id = state.get("operations.connection_id");
  let operations = state.get("oada." + connection_id + ".bookmarks.operations");

  if (operations) {
    return Promise.map(Object.keys(operations || {}), operation => {
      if (operation[0] !== "_" && operation !== "operations") {
        let op = state.get(
          `oada.${connection_id}.bookmarks.operations.${operation}`
        );
        if (op && op.id) {
          let currentOperationID = state.get(
            "User.currentSelection.operation.id"
          );
          state.set("User.operations." + operation, { id: op.id });
          if (!currentOperationID) {
            // setting default operation (if there is not one)
            state.set("User.currentSelection.operation.id", op.id);
            state.set("User.currentSelection.operation.inputText", op.label);
            state.set(
              "User.currentSelection.operation.placeholder",
              "Operation"
            );
          }
          state.set(`operations.records.${operation}`, operations[operation]);
        }
        return;
      }
    }).then(() => {
      return;
    });
  }
} //mapOadaToOperations

/**
 *
 * @param props
 * @param state
 * @returns {*}
 */
export function mapOperations({ props, state }) {
  let connection_id = state.get("operations.connection_id");
  let operations = state.get("oada." + connection_id + ".bookmarks.operations");

  if (operations) {
    return Promise.map(Object.keys(operations || {}), operation => {
      if (operation[0] !== "_" && operation !== "operations") {
        let op = state.get(
          `oada.${connection_id}.bookmarks.operations.${operation}`
        );
        if (op && op.id) {
          state.set(`operations.records.${operation}`, operations[operation]);
        }
        return;
      }
    }).then(() => {
      return;
    });
  }
} //mapOperations

/**
 * creates new operation payload
 * @param props
 * @param state
 * @returns {{id: *, operation: {created: number, id: *, text: string, tags: Array, fields: *, label: *,
 *             idh: *, geometry: {visible: boolean}, data, stats: {}}, uuid: *}}
 */
function createOperation({ props, state }) {
  let operation = {
    created: Date.now(),
    id: props.item.id, //UUID
    fields: props.item.fields || {},
    label: props.item.label
  };
  return { id: operation.id, operation, uuid: operation.id };
}

/**
 * Creates request to send to the oada-server
 * @param props
 * @param state
 * @returns {{connection_id: *, requests: Array, domain: *}}
 */
function createOperationRequest({ props, state }) {
  let connection_id = state.get("operations.connection_id");

  let request = {
    connection_id: connection_id,
    data: props.operation,
    path: _localPath + "/" + props.operation.id,
    tree
  };

  let requests = [];
  requests.push(request);
  return {
    connection_id: connection_id,
    requests: requests,
    domain: state.get("oada_domain")
  };
}

/**
 * requests the operation creation to the OADA module
 * @type {*[]}
 */
export const addOperation = [
  createOperation,
  createOperationRequest,
  oada.put,
  fetchNoWatch
];

/**
 * Creates request to change/update the resource in the oada-server
 * @param props
 * @param state
 * @returns {{connection_id: *, requests: Array, domain: string}}
 */
function buildBookmarksOperationsRequest({ props, state }) {
  let request = {
    connection_id: state.get("operations.connection_id"),
    data: {},
    type: _TYPE,
    path: _localPath,
    tree
  };

  let requests = [];

  requests.push(request);

  return {
    connection_id: state.get("operations.connection_id"),
    requests: requests,
    domain: state.get("oada_domain")
  };
}

/**
 * Creates request to change/update the resource in the oada-server
 * @param props
 * @param state
 * @returns {{connection_id: *, requests: Array, domain: string}}
 */
function buildUpdateRequest({ props, state }) {
  let operation_payload = state.get(
    `${"operations.records." + state.get("User.currentSelection.operation.id")}`
  );

  let request = {
    connection_id: state.get("operations.connection_id"),
    data: operation_payload,
    type: _TYPE,
    path: _localPath + "/" + state.get("User.currentSelection.operation.id"),
    tree
  };

  let requests = [];

  requests.push(request);

  return {
    connection_id: state.get("operations.connection_id"),
    requests: requests,
    domain: state.get("oada_domain")
  };
}

/**
 * delete request
 * @param props
 * @param state
 * @returns {{connection_id: *, requests: Array, domain: string}}
 */
function buildDeleteRequest({ props, state }) {
  let connection_id = state.get("operations.connection_id");
  let request = {
    connection_id: connection_id,
    type: _TYPE,
    path:
      _localPath +
      "/" +
      state.get("User.currentSelection.operation.id") +
      "/fields/" +
      state.get("fields.selectedId")
    //tree
  };

  let requests = [];
  requests.push(request);

  return {
    connection_id: connection_id,
    requests: requests,
    domain: state.get("oada_domain")
  };
}

export const putFieldOperation = [buildUpdateRequest, oada.put];

function updateOperationFieldState({ props, state }) {
  state.unset(
    `${"operations.records." +
      state.get("User.currentSelection.operation.id") +
      ".fields." +
      state.get("fields.selectedId")}`
  );
}

export const deleteFieldOperation = [
  buildDeleteRequest,
  oada.delete,
  updateOperationFieldState
];

/**
 * actual sequence to update operation resource
 * @type {*[]}
 */
export const updateOperation = [
  equals(props`statusValue`),
  {
    unplanned: [deleteFieldOperation],
    otherwise: [putFieldOperation]
  }
];

/**
 * Handles updates in the fields
 * @type {*[]}
 */
export const handleFieldUpdate = [
  // () => {console.log("-->operations.handlingFieldUpdate");},
  updateOperation
];
