import {state, props} from 'cerebral/tags'
import {set} from 'cerebral/operators'

export var handleChange = [
  set(state`DataTabs.value`, props`value`),
];

export var handleChangeIndex = [
  handleTabChangeIndex,
];

export var toggleTab = [
  toggleTabX,
];


export function handleTabChange({ props, state }) {
    console.log('valueX', props.value);
	state.set(`DataTabs.value`, props.value || 1);
}

function handleTabChangeIndex({props, state}) {
    //console.log('valueY', props.value);
	state.set(`DataTabs.value`, props.index);
}

function toggleTabX({props, state}) {
    //console.log('valueZ', props.value);
	state.set(`DataTabs.value`, 1 * (!(1 * state.get(`DataTabs.value`))));
}

