function handleUpdateAction({ props, state }) {
    //let operationStatus = props.operationStatus || "completed";
    let _opsList = state.get('OpsList');
    let totalArea = Object.keys(_opsList || {}).map(
          key => {
            return _opsList[key]['fields'].reduce((a, b) => {
                return a + (b.boundary ? b.boundary.area : 0)}, 0)
        }).reduce((a, b) => {return a + b}, 0);
    for(let operationStatus in _opsList){
        const area = _opsList[operationStatus]['fields'].reduce((a, b) => {return a + (b.boundary ? b.boundary.area : 0)}, 0);
        const percent = (totalArea ? Math.round(( area/totalArea + 0.00001) * 10000) / 100 : 0);
        const totalFields = _opsList[operationStatus]['fields'] ? _opsList[operationStatus]['fields'].length : 0;
        state.set(`OpsList.${operationStatus}.area`, area.toFixed(2));
        state.set(`OpsList.${operationStatus}.percentage`, percent.toFixed(2));
        state.set(`OpsList.${operationStatus}.totalFields`, totalFields);
    }//for

}

export var statsBarHandleUpdate = [
    handleUpdateAction,
];
