                {/*<IconButton*/}
                  {/*aria-label="clear field"*/}
                  {/*disabled={!(currentInputLen > 0)}*/}
                  {/*className={classes.button}*/}
                  {/*onClick={ () => { this.props.clearFieldClicked({inputText: '', inputType: 'field'})} }*/}
                  {/*style={{'paddingLeft': 12}}*/}
                {/*>*/}
                  {/*<Icon style={{'color': 'white', 'fontSize': 30 }}>clear</Icon>*/}
                {/*</IconButton>*/}
                {/*<Typography variant="title" className={classes.inputBox} noWrap>*/}
                  {/*/!*fld_input*!/*/}
