DataTabs.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(
  {
    tabValue:           state`DataTabs.value`,
    currentInputText:   state`fields.records.${state`fields.selectedId`}.name`,

    handleFieldEdited:  signal`DataTabs.handleFieldEdited`,
    handleChange:       signal`DataTabs.handleChange`,
    handleChangeIndex:  signal`DataTabs.handleChangeIndex`,
    clearFieldClicked:  signal`AutocompleteSearch.fieldInputTyped`,
  },
  withStyles(styles, { withTheme: true })(DataTabs)
);

