import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "material-ui/styles";
import classNames from "classnames";
import { connect } from "@cerebral/react";
import { state, signal } from "cerebral/tags";
import AppBar from "material-ui/AppBar";
import Toolbar from "material-ui/Toolbar";
import IconButton from "material-ui/IconButton";
import Button from "material-ui/Button";
import MenuIcon from "material-ui-icons/Menu";
import Typography from "material-ui/Typography";
import MoreVertIcon from "material-ui-icons/More";
// import ExitIcon from 'material-ui-icons/ExitToApp';
// import ClearIcon from 'material-ui-icons/ClearAll';
import AutocompleteSearch from "../AutocompleteSearch";
import AddBoxIcon from "material-ui-icons/AddBox";
//import StatsTable from '../StatsTable';
//const drawerWidth = 300;

const styles = theme => ({
  appBar: {
    display: "flex",
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    height: 64,
    order: 1
  },
  buttons: {
    // flex: 1
  },
  menuButton: {
    marginLeft: 2,
    marginRight: 2
  },
  hide: {
    display: "none"
  },
  disabled: {
    pointerEvents: "none",
    opacity: 0.4,
    touchAction: "none"
  },
  typography: {
    flex: 1,
    width: 200
  }
});

class MenuBar extends React.Component {
  render() {
    const { classes } = this.props; //, theme

    const drawer_button = (
      <IconButton
        key={0}
        color="inherit"
        aria-label="open drawer"
        onClick={() => {
          this.props.handleSideDrawerOpen({});
        }}
        className={classNames(
          classes.menuButton,
          this.props.currentOperation &&
            this.props.sideDrawerOpen &&
            classes.hide,
          !this.props.currentOperation && classes.disabled
        )}
      >
        <MenuIcon />
      </IconButton>
    );

    const settings_button = (
      <IconButton
        key={1}
        color="inherit"
        aria-label="open drawer"
        onClick={() => this.props.connectionsClicked({})}
        className={classes.menuButton}
        //component="div"
      >
        <MoreVertIcon />
      </IconButton>
    );

    const settings_button_text = (
      <Button
        key={2}
        color="inherit"
        aria-label="open drawer"
        onClick={() => this.props.connectionsClicked({})}
        className={classes.menuButton}
        disabled={this.props.connection_id === "none"}
      >
        Connect
      </Button>
    );

    // const signout_button = (
    //         <IconButton
    //             key={2}
    //             color="inherit"
    //             aria-label="open drawer"
    //             onClick={()=>this.props.signoutClicked({})}
    //             className={classes.menuButton}
    //           >
    //         <ExitIcon/>
    //         </IconButton>
    // );
    //
    // const clearcache_button = (
    //         <IconButton
    //             key={3}
    //             color="inherit"
    //             aria-label="open drawer"
    //             onClick={()=>this.props.clearCacheClicked({})}
    //             className={classes.menuButton}
    //           >
    //         <ClearIcon/>
    //         </IconButton>
    // );
    //
    const add_field = (
      <IconButton
        key={4}
        color="inherit"
        aria-label="open drawer"
        onClick={() => this.props.addFieldClicked({})}
        className={classes.menuButton}
        disabled={
          this.props.connection_id === "none" || this.props.fieldsEditing || this.props.emptyDataSet
        }
      >
        <AddBoxIcon />
      </IconButton>
    );

    const add_field_text = (
      <Button
        key={5}
        color="inherit"
        aria-label="open drawer"
        onClick={() => this.props.addFieldClicked({})}
        className={classes.menuButton}
        disabled={
          this.props.connection_id === "none" || this.props.fieldsEditing || this.props.emptyDataSet
        }
      >
        Add Field
      </Button>
    );

    const add_operator_text = (
      <Button
          key={6}
          color="inherit"
          aria-label="open drawer"
          onClick={()=>this.props.addOperatorClicked({})}
          className={classes.menuButton}
          disabled={this.props.connection_id === "none"}
      >
          Add Operator
      </Button>
    );

    const ops_input = (
      <AutocompleteSearch
        id="ops-input"
        style={{ width: 200 }}
        sourceAppBar={true}
      />
    );

    return (
      <AppBar className={classes.appBar} position="static">
        <Toolbar
          className={classes.appBar}
          disableGutters={
            !(this.props.sideDrawerOpen && this.props.currentOperation)
          }
        >
          {drawer_button}

          <Typography
            variant="title"
            color="inherit"
            style={{ align: "left" }}
            className={classes.typography}
            noWrap
          >
            {!this.props.sideDrawerOpen ? ops_input : null}
          </Typography>

          <div className={classes.buttons}>
            {(this.props.isSmall && this.props.orientation === "portrait") ? settings_button : settings_button_text}
            {(this.props.isSmall && this.props.orientation === "portrait") ? add_field : add_field_text}
            {/*{(this.props.isSmall && this.props.orientation === "portrait") ? add_operator_text : add_operator_text}*/}
          </div>
        </Toolbar>
      </AppBar>
    );
  }
}

MenuBar.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default connect(
  {
    addFieldClicked:            signal`fields.addFieldClicked`,
    operationInputTyped:        signal`AutocompleteSearch.operationInputTyped`,
    addOperationClicked:        signal`AutocompleteSearch.addOperationClicked`,
    editOperationClicked:       signal`AutocompleteSearch.handleOperationEdited`,
    operationCleared:           signal`User.handleOperationCleared`,
    addOperatorClicked:         signal`operators.addOperatorClicked`,
    handleSideDrawerOpen:       signal`SideDrawer.handleDrawerOpen`,
    connectionsClicked:         signal`MenuBar.connectionsClicked`,
    signoutClicked:             signal`MenuBar.signoutClicked`,
    clearCacheClicked:          signal`MenuBar.clearCacheClicked`,
    addNewFieldClicked:         signal`MenuBar.addNewFieldClicked`,
    showStatsClicked:           signal`StatsBar.handleOpenStats`,

    sideDrawerOpen:             state`SideDrawer.open`,
    operations:                 state`operations.records`,
    connection_id:              state`operations.connection_id`,
    appName:                    state`App.appName`,
    contentType:                state`App.contentType`,
    appIsMobile:                state`App.is_mobile`,
    emptyDataSet:               state`App.emptyDataSet`,
    orientation:                state`window.orientation`,
    isSmall:                    state`window.isSmall`,
    infoBarOperationsOpen:      state`InfoBar.operations.open`,
    fieldsEditing:              state`fields.editing`,
    operationInputText:         state`User.currentSelection.operation.inputText`,
    currentOperation:           state`User.currentSelection.operation.id`,
    operationsSuggestionsOpen:  state`User.currentSelection.operation.suggestionsOpen`
  },
  withStyles(styles, { withTheme: true })(MenuBar)
);
