/**
 * Generates a timestamp in the format 'YYYY-MM-DD'
 * @returns {string}
 */
function timestamp() {
  let today = new Date();
  let dd = today.getDate();
  let mm = today.getMonth() + 1; //January is 0!
  let yyyy = today.getFullYear();
  if (dd < 10) {
    dd = "0" + dd;
  }
  if (mm < 10) {
    mm = "0" + mm;
  }

  return yyyy + "-" + mm + "-" + dd;
}

function getCurrentOperationFieldStatus({ props, state }) {
  let id = state.get("fields.selectedId");
  let opId = state.get("User.currentSelection.operation.id");
  let result = state.get(`operations.records.${opId}.fields.${id}.status'}`);
  return result || "unplanned";
}

function setCurrentSelectionStatus({ props, state }) {
  state.set("User.currentSelection.status.status", props.value);
}

function setCurrentSelectionStatusMarkerAction({ props, state }) {
  if (!!props.marker) {
    state.set("User.currentSelection.status.marker", props.marker);
  }
}

function setCurrentSelectionStatusTimestampAction({ props, state }) {
  if (props.timestamp) {
    state.set("User.currentSelection.status.timestamp", props.timestamp);
  }
}

function saveCurrentSelectionStatusAction({ props, state }) {
  let currentOperationID = state.get("User.currentSelection.operation.id");

  //do nothing when this is falsy
  if(!currentOperationID) return;
  let id = state.get("fields.selectedId");
  let _status = state.get("User.currentSelection.status.status");

  state.set(
    `operations.records.${currentOperationID}.fields.${id}.status`,
    _status
  );

  if (_status === "unplanned") {
    state.unset(
      `${"operations.records." + currentOperationID + ".fields." + id}`
    );
  } else {
    //fixing bug when id not present in fields.id.record
    if (id) {
      state.set(
        `${"operations.records." +
          currentOperationID +
          ".fields." +
          id +
          ".id"}`,
        id
      );

      if (
        !state.get(
          `${"operations.records." +
            currentOperationID +
            ".fields." +
            id +
            ".history"}`
        )
      ) {
        state.set(
          `${"operations.records." +
            currentOperationID +
            ".fields." +
            id +
            ".history"}`,
          []
        );
      } //if

      if (
        state.get(
          `${"operations.records." +
            currentOperationID +
            ".fields." +
            id +
            ".history"}`
        )
      ) {
        state.unshift(
          `${"operations.records." +
            currentOperationID +
            ".fields." +
            id +
            ".history"}`,
          {
            status: _status || "planned",
            marker: state.get("User.currentSelection.status.marker"),
            timestamp: state.get("User.currentSelection.status.timestamp")
          }
        );
      }
    }
  } //else
}

function setCurrentOperation({ props, state }) {
  if (!!props.item) {
    state.set("User.currentSelection.operation.id", props.item.id);
    state.set("User.currentSelection.operation.inputText", props.item.label);
    state.set("User.currentSelection.operation.suggestionsOpen", false);
  }
}

function clearCurrentOperation({ props, state }) {
  state.set("User.currentSelection.operation.id", null);
}

function addNewOperationAction({ state }) {
  let _addNewOperation = true;
  let _inputText = state.get("User.currentSelection.operation.inputText");
  let _operations = state.get("operations.records");

  if (_inputText.length !== 0) {
    Object.keys(_operations || {}).forEach(function(key) {
      _inputText === _operations[key].label
        ? (_addNewOperation = false)
        : (_addNewOperation = true);
    });
  } else {
    _addNewOperation = false;
  }

  if (_addNewOperation) {
    let strUUID = uuid();
    const operationRecord = {
      id: strUUID,
      label: _inputText,
      fields: {}
    };
    state.set(`${"operations.records." + strUUID}`, operationRecord);
    state.set(`${"operations.records." + strUUID + ".id"}`, strUUID);
    return {
      item: operationRecord,
      data: operationRecord
    };
  } //if
} // addNewOperation

function setDisabledNewOperationAction({ props, state }) {
  return state.get("User.currentSelection.operation.disabled");
}

function cancelNewOperationAction({ state }) {
  const ops_id = state.get("User.currentSelection.operation.id");
  return { item: state.get(`${"operations.records." + ops_id}`) };
}

function resetCurrentSelectionStatus({ props, state }) {
  if (!state.get("fields.editing")) {
    let currentOperationFieldStatus = getCurrentOperationFieldStatus({
      props,
      state
    });
    if (currentOperationFieldStatus) {
      state.set(
        "User.currentSelection.status.status",
        currentOperationFieldStatus
      )
    }
    state.set("User.currentSelection.status.timestamp", timestamp());
    state.set("User.currentSelection.status.marker", state.get("User.label"));
  }
}
