import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import { connect } from '@cerebral/react';
import { state, signal} from 'cerebral/tags';
import _ from 'lodash';
import TextField from 'material-ui/TextField';
import { InputAdornment } from 'material-ui/Input';
import Paper from 'material-ui/Paper';
import { MenuItem } from 'material-ui/Menu';
import IconButton from 'material-ui/IconButton';
import Icon from 'material-ui/Icon';
import Downshift from 'downshift';
import ArrowDropDown from "material-ui-icons/ArrowDropDown";

const styles = theme => ( {
  container: {
    display: 'flex',
    flexDirection: 'row',
    flexGrow: 1,
    alignItems: 'center',
    height: 250,
    width: 50,
  },
  inputBox: {
    display: 'flex',
    flexDirection: 'row',
    flexGrow: 1,
    alignItems: 'center',
    //height: 50,
    //width: 220,
  },
  suggestionsContainerOpen: {
    position: 'absolute',
    width: 180,
    marginTop: theme.spacing.unit * 0.1,
    marginBottom: theme.spacing.unit * 0,
    marginLeft: 30,
    marginRight: 0,
    zIndex: 3,
  },
  suggestion: {
    display: 'block',
  },
  suggestionsList: {
    marginTop: theme.spacing.unit * 0.1,
    marginBottom: theme.spacing.unit * 0,
    marginLeft: 0,
    marginRight: 0,
    padding: 0,
    listStyleType: 'none',
  },
  disabled: {
    pointerEvents: 'none',
    opacity: 0.4,
    touchAction: 'none',
  },
  menuButton: {
    marginLeft: 2,
    marginRight: 2
  }
});

let temp = "FieldWorkApp";
//this.props.operationSuggestions[this.props.currentOperationId].label

class AutocompleteSearch extends React.Component {

  renderInput(inputProps) {
    const { InputProps, classes, isMenuBar, ref, ...other } = inputProps;
    return (
      <TextField
        onFocus={() => {this.props.openSuggestions({id: this.props.id})}}
        onBlur={() => {this.props.closeSuggestions({id: this.props.id})}}
        value={temp}
        {...other}
        inputRef={ref}
        InputProps={{
          style: {color: '#FAFAFA'},
          classes: {
            input: classes.input,
          },
          //startAdornment: (<InputAdornment position="start"><Icon>search</Icon></InputAdornment>),
          endAdornment: (
            <InputAdornment position="start">
              <IconButton
                aria-label="clear textfield"
                className={classes.menuButton}
                onClick={ () => {
                  if (this.props.id === 'ops-input') {
                    this.props.operationInputTyped({inputText: "", inputType: "operation"});
                  } else {
                    this.props.fieldInputTyped({inputText: "", inputType: "field"});
                  }
                } }
              >
                <ArrowDropDown />
              </IconButton>
            </InputAdornment>
          ),
          ...InputProps,
        }}
        label={this.props.id === 'ops-input' ? (isMenuBar ? "" : "Operation") : "Field"}
        InputLabelProps={{style: {color: 'white'}, shrink: true}}
      />
    );
  }

  getSuggestions(inputValue) {
    let suggestions = [];

    if (this.props.id === 'ops-input') {
        //console.log('-->operationSuggestions', this.props.operationSuggestions);
      suggestions = Object.values(this.props.operationSuggestions || {}).filter(suggestion => (
        inputValue === "" || _.find(this.props.operationSuggestions, s => s.label === inputValue) ||
        // inputValue === "" || Object.values(this.props.operationSuggestions).map(suggestion => (suggestion.label)).includes(inputValue) ||
          suggestion.label.toLowerCase().includes(inputValue.trim().toLowerCase())
      ));

        if(this.props.connection_id !== "none")
            suggestions.unshift({'action': 'add', type: 'operation', label: 'add new operation'});

      return suggestions;
    }//if

    if (this.props.id === 'fld-input') {
      suggestions = Object.values(this.props.fieldSuggestions).filter(suggestion => (
        inputValue === "" || _.find(this.props.fieldSuggestions, s => s.label === inputValue) ||
        suggestion.label.toLowerCase().includes(inputValue.trim().toLowerCase())
      ));

      /* add button at the beginning of the drop down menu */
      suggestions.unshift({'action': 'add', type: 'field', label: 'add new field'});
      return suggestions;
    }
  }//getSuggestions

  renderSuggestion(params) {
    const { suggestion, index, itemProps, highlightedIndex, selectedItem } = params;
    const isHighlighted = highlightedIndex === index;
    const isSelected = selectedItem === suggestion.label;

    if ('action' in suggestion && suggestion.action === 'add') {
      return (
        <MenuItem
          {...itemProps}
          key={suggestion.label}
          onClick={(event) => {
            (this.props.id === 'ops-input') ?
              this.props.addOperationClicked({}) :
              this.props.addFieldClicked({})
            }
          }
          selected={isHighlighted}
          component="div"
          style={{
            display: 'block',
            color: 'green',
            fontStyle: 'italic',
            fontWeight: isSelected ? 500 : 400,
          }}
        >
          {suggestion.label}
        </MenuItem>
      );
    } else {
      return (
        <MenuItem
          {...itemProps}
          key={suggestion.label}
          onClick={(event) => {
            (this.props.id === 'ops-input') ?
              this.props.operationSelected({item: suggestion}) :
              this.props.fieldSelected({item: suggestion})
            }}
          selected={isHighlighted}
          component="div"
          style={{
            display: 'block',
            fontWeight: isSelected ? 500 : 400,
          }}
        >
          {suggestion.label}
        </MenuItem>
      );
    }
  }

  renderAddButton(params) {
    const { classes, addOperationDisabled, addFieldDisabled } = params;
    return (
      <IconButton
        aria-label="add"
        className={classes.button}
        disabled={this.props.id === 'ops-input' ? addOperationDisabled : addFieldDisabled}
        onClick={
          this.props.id === 'ops-input' ?
            () => {this.props.addOperationClicked({})} :
            () => {this.props.addFieldClicked({})}
        }
      >
        <Icon style={{'color': 'white', 'fontSize': 32}}>add_circle</Icon>
      </IconButton>
    )
  }

  renderEditButton(params) {
    const { classes } = params;
    return (
      <IconButton
        aria-label="fields"
        disabled={this.props.id === 'ops-input' ? !(this.props.currentOperationId) : !(this.props.currentFieldId)}
        className={classes.button}
        onClick={
          this.props.id === 'ops-input' ?
            (event) => { this.props.editOperationClicked({event: event}) } :
            (event) => { this.props.editFieldClicked({event: event}) }
        }
      >
        <Icon style={{'color': 'white', 'fontSize': 32}}>mode_edit</Icon>
      </IconButton>
    )
  }

  isAddOperationDisabled() {
    const ret = (
      (this.props.operationInputText.trim() === "") ||
      (
        Object.values(this.props.operationSuggestions).map(operation => {
          return operation.label.toLowerCase();
        }).includes(this.props.operationInputText.trim().toLowerCase())
      )
    );
    if (ret) { console.log("AddOperationDisabled"); }
    if (!ret) { console.log("AddOperationEnabled"); }
  }


  render() {
    const { classes, sourceAppBar } = this.props;

    return(
      <Downshift
        isOpen={this.props.id === 'ops-input' ? this.props.operationsSuggestionsOpen : this.props.fieldsSuggestionsOpen}
        selectedItem={this.props.id === 'ops-input' ? this.props.currentOperationId : this.props.currentFieldId}
        inputValue={this.props.id === 'ops-input' ? this.props.operationInputText : this.props.fieldInputText}
        itemToString={(item) => (item == null ? '' : item.label)}
        onStateChange={(changes, stateAndHelpers) => {

          const type = (this.props.id === 'ops-input') ? 'operation' : 'field';
          switch (changes.type) {
            case Downshift.stateChangeTypes.keyDownEnter:
            case Downshift.stateChangeTypes.clickItem:
              if (type === 'operation') {
                this.props.operationSelected({item: changes.selectedItem});
              } else {
                this.props.fieldSelected({item: changes.selectedItem});
              }
              break;
            case Downshift.stateChangeTypes.changeInput:
              if (type === 'operation') {
                this.props.operationInputTyped({inputText: changes.inputValue, inputType: type});
              } else {
                this.props.fieldInputTyped({inputText: changes.inputValue, inputType: type});
              }
              break;
            case Downshift.stateChangeTypes.mouseUp:
            case Downshift.stateChangeTypes.blurInput:
              if (type === 'operation') {
                if (!(!this.props.currentOperationId) && this.props.operationInputText === "") {
                  //this.props.operationSelected({item: this.props.operationSuggestions[this.props.currentOperationId]});
                }
              } else {
                if (!(!this.props.currentFieldId)) {
                  this.props.fieldSelected({item: this.props.fieldSuggestions[this.props.currentFieldId]});
                }
              }
              break;
            default:
              break;
          }
        }}
      >
        {({ getInputProps, getItemProps, isOpen, inputValue, selectedItem, highlightedIndex }) => (
          <div>

            <div className={classes.inputBox}>
              {this.renderInput({
                isMenuBar: sourceAppBar,
                fullWidth: false,
                classes,
                InputProps: getInputProps({
                  placeholder: (this.props.id === 'ops-input' ? 'Operation' : (this.props.id === 'fld-input' ? 'Field' : this.props.id)),
                  id: 'autocomplete-search-' + this.props.id,
                }),
              })}
            </div>
            {isOpen ? (
              <Paper square className={classes.suggestionsContainerOpen}>
                <div className={classes.suggestionsList}>
                  {this.getSuggestions(inputValue).map((suggestion, index) =>
                    this.renderSuggestion({
                      suggestion,
                      index,
                      itemProps: getItemProps({
                        item: suggestion
                      }),
                      highlightedIndex,
                      selectedItem
                    }),
                  )}
                </div>
              </Paper>
            ) : null}
          </div>
        )}
      </Downshift>
    );
  }
}

AutocompleteSearch.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  {
    operationSuggestions:       state`operations.records`,
    connection_id:              state`operations.connection_id`,
    currentOperationId:         state`User.currentSelection.operation.id`,
    operationInputText:         state`User.currentSelection.operation.inputText`,
    operationsSuggestionsOpen:  state`User.currentSelection.operation.suggestionsOpen`,
    fieldSuggestions:           state`fields.records`,
    currentFieldId:             state`fields.selectedId`,
    fieldsSuggestionsOpen:      state`fields.new_field.field.suggestionsOpen`,
    fieldInputText:             state`fields.records.${state`fields.selectedId`}.name`,

    operationInputTyped:        signal`AutocompleteSearch.operationInputTyped`,
    fieldInputTyped:            signal`AutocompleteSearch.fieldInputTyped`,
    addOperationClicked:        signal`AutocompleteSearch.addOperationClicked`,
    editOperationClicked:       signal`AutocompleteSearch.handleOperationEdited`,
    editFieldClicked:           signal`AutocompleteSearch.handleFieldEdited`,
    openSuggestions:            signal`AutocompleteSearch.openSuggestions`,
    closeSuggestions:           signal`AutocompleteSearch.closeSuggestions`,
    operationSelected:          signal`User.handleOperationSelected`,
    operationCleared:           signal`User.handleOperationCleared`,
    addFieldClicked:            signal`User.addFieldClicked`,
    fieldSelected:              signal`fields.handleFieldSelected`,
    fieldCleared:               signal`fields.handleFieldCleared`,

  },
  withStyles(styles, { withTheme: true })(AutocompleteSearch)
);
