export const resized = [
  ({ state, props }) => {
    //console.log("--> window module state, props-->", state, props);
    const orientation = props.width > props.height ? "landscape" : "portrait";
    if (state.get("window.orientation") !== orientation) {
      state.set("window.orientation", orientation);
    }

    const isSmall =
      orientation === "portrait" ? props.width < 767 : props.height < 500;
    if (state.get("window.isSmall") !== isSmall)
      state.set("window.isSmall", isSmall);
  }
];

export const init = [
  ({ windowSize }) => windowSize.init("window.resized"), // registers event listener for window resize events
  resized // load dimensions the first time
];