import { set } from "cerebral/operators";
import { state } from "cerebral/tags";
import gjArea from "@mapbox/geojson-area";
import { validateNewFieldButton } from "../fields/sequences";

export var startMovingMap = [set(state`MapPanel.moving`, true)];

export var doneMovingMap = [set(state`MapPanel.moving`, false)];

export var handleMapClick = [
  dropPoint,
  recalculateArea,
  getFieldBoundingBox,
  validateNewFieldButton
];

export var handleLocationFound = [setCurrentLocation];

export var undoDrawPoint = [undo, recalculateArea, getFieldBoundingBox];

export var endMarkerDrag = [set(state`MapPanel.dragging_marker`, false)];

export var startMarkerDrag = [set(state`MapPanel.dragging_marker`, true)];

export var markerDragging = [
  setMarkerPosition,
  recalculateArea,
  getFieldBoundingBox
];

export var handleCurrentLocationButton = [setMapToCurrentLocation];

export var handleMapMoved = [setMapLocation];

export var setFieldOnMapCenter = [mapToFieldPolygon];

export var deleteCoordinate = [
  deleteCoordinateAction,
  recalculateArea,
  getFieldBoundingBox
];

function deleteCoordinateAction({ props, state }) {
  //let id = state.get("fields.selectedId");
  let tempObject = state.get(`fields.new_field`);
  if (tempObject.boundary.geojson.coordinates[0].length > props.index) {
    tempObject.boundary.geojson.coordinates[0].splice(props.index, 1);
    state.set(`fields.new_field`, tempObject);
  }
}

function setMapToCurrentLocation({ state }) {
  let loc = state.get("MapPanel.current_location");
  if (loc) state.set("MapPanel.center", [loc.lat, loc.lng]);
}

function setMapLocation({ props, state }) {
  state.set("MapPanel.center", [props.latlng.lat, props.latlng.lng]);
  state.set("MapPanel.zoom", props.zoom);
}

function setCurrentLocation({ props, state }) {
  let obj = {
    lat: props.lat,
    lng: props.lng
  };
  state.set("MapPanel.current_location", obj);
}

