function setMarkerPosition({ props, state }) {
  //let id = state.get(`fields.selectedId`);
  state.set(`fields.new_field.boundary.geojson.coordinates.0.${props.idx}`, [
    props.lng,
    props.lat
  ]);
}

function validateFieldCoordinates({ state }) {
  //let id = state.get(`fields.selectedId`);
  let field = state.get(`fields.new_field`);
  //console.log("--> validating field coordinates");

  if (
    field.boundary.geojson &&
    field.boundary.geojson.coordinates[0].length > 2
  ) {
    state.set("User.currentSelection.field.coordinatesOk", true);
  } else {
    state.set("User.currentSelection.field.newFieldDisabled", true);
    state.set("User.currentSelection.field.coordinatesOk", false);
  }
}

function recalculateArea({ state }) {
  //let id = state.get(`fields.selectedId`);
  let field = state.get(`fields.new_field`);
  let area = 0.0;
  if (
    field.boundary.geojson &&
    field.boundary.geojson.coordinates[0].length > 2
  ) {
    area = gjArea.geometry(field.boundary.geojson) / 4046.86;
  }
  state.set(`fields.new_field.boundary.area`, area);

  validateFieldCoordinates({ state });
}

function undo({ props, state }) {
  //let id = state.get("fields.selectedId");
  let points = state.get(`fields.new_field.boundary.geojson.coordinates.0`);
  if (points.length > 0) {
    state.pop(`fields.new_field.boundary.geojson.coordinates.0`);
  }
}

function mapToFieldPolygon({ state }) {
  // let id = state.get("fields.selectedId");
  // console.log("--> selectedId", id);
  // if (!state.get("fields.editing")) {
  //   let field = state.get(`fields.new_field`);
  //   if (field) {
  //     state.set("MapPanel.center", field.boundary.centroid);
  //   }
  // }
  let id = state.get("fields.selectedId");
  if (!state.get("fields.editing")) {
    let field = state.get(`fields.records.${id}`);
    if (field) {
      state.set("MapPanel.center", field.boundary.centroid);
    }
  }
}

function getFieldBoundingBox({ props, state }) {
  //let id = state.get("fields.selectedId");
  let bbox = computeBoundingBox(state.get(`fields.new_field.boundary.geojson`));
  let c1 = null;
  let c2 = null;
  if (!!bbox) {
    c1 = (bbox.north + bbox.south) / 2;
    c2 = (bbox.east + bbox.west) / 2;
  }
  state.set(`fields.new_field.boundary.bbox`, bbox);
  state.set(`fields.new_field.boundary.centroid`, [c1, c2]);
}

function dropPoint({ props, state }) {
  //let id = state.get("fields.selectedId");
  state.push(`fields.new_field.boundary.geojson.coordinates.0`, props.pt);
}

function computeBoundingBox(geojsonPolygon) {
  let coords = geojsonPolygon.coordinates[0];
  if (coords.length > 0) {
    let north = coords[0][1];
    let south = coords[0][1];
    let east = coords[0][0];
    let west = coords[0][0];
    for (let j = 0; j < coords.length; j++) {
      if (coords[j][1] > north) north = coords[j][1];
      if (coords[j][1] < south) south = coords[j][1];
      if (coords[j][0] > east) east = coords[j][0];
      if (coords[j][0] < west) west = coords[j][0];
    }
    let bbox = { north, south, east, west };
    return bbox;
  } else {
    return null;
  }
}
