/* internal local variables */
const _METERS_TO_ACRES = 0.000247105;

let tree = {
  bookmarks: {
    _type: "application/vnd.oada.bookmarks.1+json",
    _rev: "0-0",
    fields: {
      _type: "application/vnd.oada.fields.1+json",
      _rev: "0-0",
      "fields-index": {
        "*": {
          _type: "application/vnd.oada.field.1+json",
          _rev: "0-0",
          "fields-index": {
            "*": {
              _type: "application/vnd.oada.field.1+json",
              _rev: "0-0"
            }
          }
        }
      }
    }
  }
};

