/**
 * Maps fields records
 * @param props
 * @param state
 * @returns {*}
 */
export function mapOadaToFields({ props, state }) {
  let connection_id = state.get("fields.connection_id");
  let curFields = state.get("fields.records");

  //console.log(" ===== map oada to fields ===== ");

  return Promise.map(Object.keys(curFields || {}), field => {
    return state.unset(`fields.records.${field}`);
  })
    .then(() => {
      let fields = state.get(`oada.${connection_id}.bookmarks.fields`);
      if (fields) {
        return Promise.map(
          Object.keys(fields["fields-index"] || {}),
          fieldGroup => {
            if (fields["fields-index"][fieldGroup]) {
              return Promise.map(
                Object.keys(
                  fields["fields-index"][fieldGroup]["fields-index"] || {}
                ),
                field => {
                  let field_object =
                    fields["fields-index"][fieldGroup]["fields-index"][field];
                  let record = {
                    id: field,
                    name: field_object.name,
                    farm: {
                      id: fieldGroup,
                      name: fields["fields-index"][fieldGroup].name
                    }
                  };

                  if (field_object.boundary) {
                    let centroid_object = centroid(
                      field_object.boundary.geojson
                    );
                    record.boundary = {
                      geojson: field_object.boundary.geojson,
                      area:
                        geojson_area.geometry(field_object.boundary.geojson) *
                        _METERS_TO_ACRES,
                      centroid: centroid_object.geometry.coordinates.reverse(),
                      bbox: bbox(field_object.boundary.geojson),
                      visible: true
                    };
                  }

                  return state.set(`fields.records.${field}`, record);
                }
              );
            } else {
              return;
            }
          }
        );
      } else return;
    })
    .then(() => {
      return;
    });
}

/**
 * Maps farms records (utilized to validate the existence of a farm)
 * @param props
 * @param state
 * @returns {*}
 */
export function mapOadaToFarms({ props, state }) {
  let connection_id = state.get("fields.connection_id");
  let curFarms = state.get("fields.farms");

  //console.log(" ---- map oada to farms ---- ");

  return Promise.map(Object.keys(curFarms || {}), farm => {
    return state.unset(`fields.farms.${farm}`);
  })
    .then(() => {
      let fields = state.get(`oada.${connection_id}.bookmarks.fields`);
      if (fields) {
        return Promise.map(Object.keys(fields["fields-index"] || {}), farm => {
          if (
            fields["fields-index"][farm] &&
            fields["fields-index"][farm].name
          ) {
            let farm_object = fields["fields-index"][farm];
            let record = {
              id: farm,
              name: farm_object.name
            };
            return state.set(
              `fields.farms.${fields["fields-index"][farm].name}`,
              record
            );
          } else {
            return;
          }
        });
      } else return;
    })
    .then(() => {
      return;
    });
} //mapOadaToFarms

export const putFarm = [
  ({ state, props }) => ({
    data: {
      _id: "resources/" + props.item.farm.id,
      name: props.item.farm.name
    }
  }),
  ({ props, state }) => ({
    connection_id: state.get("operations.connection_id"),
    data: props.data,
    path: `/bookmarks/fields/fields-index/${props.item.farm.id}`,
    tree
  }),
  createFarmRequest,
  oada.put
];

/**
 *
 * @param props
 * @param state
 * @returns {{connection_id: *, data: {id: null, placeholder: string, suggestionsOpen: boolean, name: string, disabled: boolean, editing: boolean, tempObject: null, newFieldDisabled: boolean}|state.currentSelection.field|{id, placeholder, suggestionsOpen, name, disabled, editing, tempObject, newFieldDisabled}|*|{_id: string, name: *, boundary: {geojson: {type: string, coordinates: Array[]}|boundary.geojson|{type, coordinates}}}|{id: *, label: *, boundary: {area: null, geojson: {type: string, coordinates: *[]}, bbox: null, centroid: *[], visible: boolean}}, path: string, tree: {bookmarks: {_type: string, _rev: string, fields: {_type: string, _rev: string, "fields-index": {"*": {_type: string, _rev: string, "fields-index": {"*": {_type: string, _rev: string}}}}}}}}}
 */
function createFarmRequest({ props, state }) {
  let data = {
    _id: "resources/" + props.item.farm.id,
    name: props.item.farm.name
  };
  let requests = [];
  requests.push({
    connection_id: state.get("operations.connection_id"),
    data,
    path: `/bookmarks/fields/fields-index/${props.item.farm.id}`,
    tree
  });
  return { requests };
}

/**
 * creates new field payload
 * @param props
 * @param state
 * @returns {{id: *, field: {created: number, id: *, text: string, tags: Array, fields: {},
 * boundary: {visible: boolean}, data, stats: {}}, uuid: *}}
 */
function createField({ props, state }) {
  let field = {
    _id: "resources/" + props.item.id,
    name: props.item.name,
    boundary: { geojson: props.item.boundary.geojson }
  };
  return { id: props.item.id, field };
}

function createFieldRequest({ props, state }) {
  let farmName = state.get(`fields.farms.${props.item.farm.name}`);
  let farmId = props.item.farm.id;

  if (farmName) {
    farmId = state.get(`fields.farms.${props.item.farm.name}.id`);
  }

  let requests = [];

  requests.push({
    connection_id: state.get("operations.connection_id"),
    data: props.field,
    path: `/bookmarks/fields/fields-index/${farmId}/fields-index/${props.id}`,
    tree
  });

  return { requests };
}

