function setStatusMarkerCurrentField({ props, state }){
  let operationId = state.get(`User.currentSelection.operation.id`);
  let fieldId = state.get(`fields.selectedId`);
  let status = state.get(`operations.records.${operationId}.fields.${fieldId}.status`);
  let marker = state.get(`operations.records.${operationId}.fields.${fieldId}.history.0.marker`);
  if(status)
    state.set(`User.currentSelection.status.status`, status);
  if(marker) {
    state.set(`User.currentSelection.status.marker`, marker);
    state.set(`User.label`, marker);
  }
}

export function setCurrentField({ props, state }) {
  state.set("fields.selectedId", props.field.id);
  if (!state.get("fields.editing")) {
    state.set(`fields.new_field.field.suggestionsOpen`, false);
  }
}

