export var loadData = [
	loadDataAction,
	//...dataTabsHandleChange,
];

export var handleCollapse = [
	handleCollapseAction,
];

export var handleChronologyCollapse = [
	handleChronologyCollapseAction,
];

function handleCollapseAction({ props, state }) {
	state.toggle(`${'FldList.' + props.field_id + '.operations.' + props.operation_id + '.collapse'}`);
}

function handleChronologyCollapseAction({props, state}) {
	state.toggle(`${'FldList.' + props.field_id + '.chronologyCollapse'}`);
}

function loadDataAction({ props, state }) {
	if (!(state.get('fields.editing'))) {
		const all_fields = Object.keys(state.get('fields.records'));

		all_fields.map(fld_id => {
            let operationsRecords = state.get(`${'operations.records'}`);
			if( operationsRecords )
			state.set(`${'FldList.' + fld_id + '.operations'}`,
				Object.values(operationsRecords || {}).filter(operationRecord => {
					return operationRecord.hasOwnProperty('fields') && operationRecord.fields.hasOwnProperty(fld_id)
				}).reduce((obj, operation) => {obj[operation.id] = {'id': operation.id, 'collapse': true}; return obj}, {})
			);
		});

		if (props.switchTab) {
			return {value: props.switchTab}
		} else {
			return {value: 1}
		}
	} else {
		return {value: 2}
	}
}
