import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "material-ui/styles";
import { connect } from "@cerebral/react";
import { state, signal } from "cerebral/tags";
import uuid from "uuid";
import "./map.css";
// import { Icon, Typeography, Button, TextField} from 'material-ui/Paper';
import {
  Marker,
  Map,
  TileLayer,
  GeoJSON,
  FeatureGroup,
  LayersControl
} from "react-leaflet";
const { Overlay } = LayersControl;
//import './map.css';
//const editDoneButtonHeight = 64;

const styles = theme => ({
  "leaflet-container": {
    flex: 1,
    order: 2,
    flexGrow: 1,
    width: "100%"
  },
  mapContainer: {
    display: "flex",
    flexGrow: 1,
    order: 2
  },
  map: {
    display: "flex",
    width: "100%",
    height: "100%"
  },
  fieldPolygon: {
    strokeWidth: 4,
    strokeOpacity: 1,
    width: "100vw"
  },
  disabled: {
    pointerEvents: "none",
    opacity: 0.4,
    touchAction: "none"
  },
  hide: {
    display: "none"
  }
});

class MapPanel extends React.Component {
  validateMouseEvent(evt) {
    if (this.props.fieldEditing) {
      // Don't fire a click event when panning the map or when dragging a point.
      if (!this.props.moving && !this.props.dragging) {
        // Don't add a point if a control was clicked.
        //console.log("originalEvent", evt.originalEvent);
        if (!evt.originalEvent.target.offsetParent) {
          this.props.mouseDownOnMap({ pt: [evt.latlng.lng, evt.latlng.lat] });
        } else if (
          !evt.originalEvent.target.offsetParent.className.includes("marker")
        ) {
          this.props.mouseDownOnMap({ pt: [evt.latlng.lng, evt.latlng.lat] });
        }
      }
    }
  }

  componentDidMount() {
    this.refs.map.leafletElement.locate();
  }

  render() {
    const { classes } = this.props; // , theme

    const color_code = {
      unplanned: {
        color1: "#E0E0E0",
        bannerText: "Unplanned",
        color2: "#616161"
      },
      planned: {
        color1: "#D50000",
        bannerText: "Planned",
        color2: "#EF9A9A"
      },
      started: {
        color1: "#FFC107",
        bannerText: "Started",
        color2: "#FFC107"
      },
      completed: {
        color1: "#76FF03",
        bannerText: "Completed",
        color2: "#33691E"
      }
    };

    let markerList = [];
    if (this.props.fieldEditing) {
      //console.log("-->currentField", this.props.currentEditingField);
      if (
        this.props.currentField &&
        this.props.currentField.boundary &&
        this.props.currentField.boundary.geojson.coordinates[0].length > 0
      ) {
        markerList = [];
        this.props.currentField.boundary.geojson.coordinates[0].forEach(
          (pt, i) => {
            markerList.push(
              <Marker
                className={"selected-field-marker"}
                key={this.props.selectedId + "-" + i}
                position={[pt[1], pt[0]]}
                color={
                  this.props.currentOperationId &&
                  this.props.allOperations[this.props.currentOperationId] &&
                  this.props.allOperations[this.props.currentOperationId]
                    .fields[this.props.selectedId]
                    ? this.props.allOperations[this.props.currentOperationId]
                        .fields[this.props.selectedId].status
                    : "unplanned"
                }
                draggable={true}
                onDrag={e => {
                  this.props.markerDragged({
                    lat: e.target._latlng.lat,
                    lng: e.target._latlng.lng,
                    idx: i
                  });
                }}
                onDragStart={e => {
                  this.props.markerDragStarted({ idx: i });
                }}
                onDragEnd={e => {
                  this.props.markerDragEnded({
                    lat: e.target._latlng.lat,
                    lng: e.target._latlng.lng,
                    idx: i
                  });
                }}
                onDblClick={e => {
                  this.props.deleteCoordinate({ index: i });
                }}
              />
            );
          }
        );
      }
    }

    let new_field_polygon = this.props.currentField.boundary ? (
      <GeoJSON
        className={classes.fieldPolygon}
        data={this.props.currentField.boundary.geojson}
        onClick={() => {
          if (!(this.props.selectedId && 
                this.props.currentField.id === this.props.selectedId && 
                this.props.fieldEditing)
          ) {
            this.props.handleFieldPolygonClicked({
              id: this.props.currentField.id,
              field: this.props.currentField
            });
          }
          this.forceUpdate();
        }}
        key={"field-" + this.props.currentField.id + "-polygon" + uuid()}
        color={color_code[this.props.currentOperation.fields[this.props.currentField.id] ?
          this.props.currentOperation.fields[this.props.currentField.id].status : "unplanned"].color1}
        style={{ fillOpacity: 0.4 }}
      />
    ) : null;

    return (
      <div className={classes.mapContainer}>
        <Map
          onLocationfound={e =>
            this.props.locationFound({ lat: e.latlng.lat, lng: e.latlng.lng })
          }
          onClick={e => {
            this.validateMouseEvent(e);
          }}
          onMoveStart={e => {
            this.props.mapMoveStarted();
          }}
          onMoveend={e => {
            this.props.mapMoved({
              latlng: this.refs.map.leafletElement.getCenter(),
              zoom: this.refs.map.leafletElement.getZoom()
            });
          }}
          dragging={true}
          ref="map"
          center={this.props.center}
          attributionControl={!this.props.isMobile}
          zoomControl={true /*!this.props.isMobile*/}
          zoom={this.props.mapZoom || 15}
        >
          <TileLayer
            key={"tile-layer"}
            url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"
            attribution="Tiles &copy; Esri &mdash; Source: Esri, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community"
          />

          {markerList}
          {new_field_polygon}
          <LayersControl position="topright">
            {
              <Overlay checked name="Fields">
                <FeatureGroup>
                  {Object.keys(this.props.fields || {})
                    .filter(id => {
                      return (
                        this.props.currentOperationId &&
                        this.props.fields[id].boundary &&
                        (id !== this.props.selectedId ||
                          !this.props.fieldsEditing)
                        //this.props.fields[id].boundary.visible
                      );
                    })
                    .map(id => ( id === this.props.currentField.id ? null :
                      <GeoJSON
                        className={classes.fieldPolygon}
                        data={this.props.fields[id].boundary.geojson}
                        onClick={() => {
                          if (!(this.props.selectedId &&
                                id === this.props.selectedId &&
                                this.props.fieldEditing)
                          ) {
                              // if editing, do not handleFieldPolygonClicked
                              if(!this.props.fieldEditing)
                                this.props.handleFieldPolygonClicked({id, field: this.props.fields[id]});
                          }
                          this.forceUpdate();
                        }}
                        key={"field-" + id + "-polygon" + uuid()}
                        color={
                          color_code[
                            this.props.currentOperation &&
                            this.props.currentOperation.fields &&
                            this.props.currentOperation.fields[id] &&
                            this.props.currentOperation.fields[id].status
                              ? this.props.currentOperation.fields[id].status
                              : "unplanned"
                          ].color1
                        }
                        style={{ fillOpacity: 0.4 }}
                      />
                    ))}
                </FeatureGroup>
              </Overlay>
            }
          </LayersControl>
        </Map>
      </div>
    );
  } //render
}

MapPanel.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default connect(
  {
    mapZoom:                    state`MapPanel.zoom`,
    moving:                     state`MapPanel.moving`,
    isLoading:                  state`MapPanel.isLoading`,
    isMobile:                   state`App.is_mobile`,
    geohashPolygons:            state`MapPanel.geohashPolygons`,
    center:                     state`MapPanel.center`,
    sideDrawerOpen:             state`SideDrawer.open`,
    currentField:               state`fields.new_field`,
    fields:                     state`fields.records`,
    fieldEditing:               state`fields.editing`,
    selectedId:                 state`fields.selectedId`,
    currentOperationId:         state`User.currentSelection.operation.id`,
    currentStatus:              state`User.currentSelection.status.status`,
    allOperations:              state`operations.records`,
    operations:                 state`operations.records`,
    currentOperation:           state`operations.records.${state`User.currentSelection.operation.id`}`,

    mapMoveStarted:             signal`MapPanel.mapMoveStarted`,
    mouseDownOnMap:             signal`MapPanel.mouseDownOnMap`,
    markerDragStarted:          signal`MapPanel.markerDragStarted`,
    markerDragEnded:            signal`MapPanel.markerDragEnded`,
    markerDragged:              signal`MapPanel.markerDragged`,
    locationFound:              signal`MapPanel.locationFound`,
    mapMoved:                   signal`MapPanel.mapMoved`,
    deleteCoordinate:           signal`MapPanel.deleteCoordinate`,
    handleFieldPolygonClicked:  signal`fields.handleFieldPolygonClicked`,
    saveEdited:                 signal`fields.saveEditedField`,

  },
  withStyles(styles, { withTheme: true })(MapPanel)
);
