import { handleChange as dataTabsHandleChange } from '../DataTabs/chains';

export var loadData = [
	floadData,
	dataTabsHandleChange,
];

export var clearData = [
	fclearData,
];

function handleCollapseAction({ props, state }) {
    state.toggle(`${'OpsList.' + props.status + '.collapse'}`);
}

export var handleCollapse = [
	handleCollapseAction,
];

function floadData({ props, state }) {
	const opid = state.get('User.currentSelection.operation.id');
	let fields = [];
	if (!(!opid)) {
		fields = Object.values(state.get(`${'operations.records.' + opid + '.fields'}`));
	}
	Object.keys(state.get('OpsList') || {}).map(status => {
		state.set(
			`${'OpsList.' + status + '.fields'}`,
			fields.filter(field => (field.status === status)).map(fieldStub => {
				return {'since': fieldStub['history'] ? fieldStub['history'][0]['timestamp'] : "",
						'by': fieldStub['history'] ? fieldStub['history'][0]['marker'] : "",
                        'id': fieldStub['id'] ? fieldStub['id'] : "",
						...state.get(`fields.records.${fieldStub['id']}`)};
			})
		);
	});
	return {value: 0};
}

function fclearData({props, state}) {
	Object.keys(state.get('OpsList')).map(status => {
		state.set(`${'OpsList.' + status + '.fields'}`, []);
	});
}
