import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import { connect } from '@cerebral/react';
import {state, signal} from 'cerebral/tags'
import Drawer from 'material-ui/Drawer';
import IconButton from 'material-ui/IconButton';
import ChevronLeftIcon from 'material-ui-icons/ChevronLeft';
import Typography from 'material-ui/Typography';
import AutocompleteSearch from '../AutocompleteSearch'
import DataTabs from '../DataTabs'

const drawerWidth = 250;

const styles = theme => ({
  hide: {
    display: 'none',
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor: '#212121',
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'row',
    justifyContent: 'flex-start',
    paddingLeft: '15px',
    ...theme.mixins.toolbar,
  },
  drawerInner: {
    padding: '0px',
  },
  inputContainer: {
  	display: 'flex',
    justifyContent: 'flex-start',
  	flexDirection: 'column',
    marginTop: theme.spacing.unit,
    width: 200
  }
 });

class SideDrawer extends React.Component {

	render() {
    const { classes } = this.props;
    const ops_input = (<AutocompleteSearch id='ops-input' />);
    const data_tabs = (<DataTabs />);

    return (
      <Drawer
        variant="persistent"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor={'left'}
        open={this.props.sideDrawerOpen && (!(!this.props.currentOperation)) }
      >
        <div className={classes.drawerInner}>
          <div className={classes.drawerHeader}>
            <div className={classes.inputContainer}>
	            <Typography variant="title" noWrap>
	              {ops_input}
	            </Typography>
            </div>
          <IconButton onClick={() => {this.props.handleSideDrawerOpen({})}}>
            <ChevronLeftIcon style={{'color': '#FAFAFA', 'fontSize': 35}}/>
          </IconButton>
          </div>
          {data_tabs}
        </div>
      </Drawer>
    );
  }
}

SideDrawer.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(
	{
    sideDrawerOpen:       state`SideDrawer.open`,
    currentOperation:     state`User.currentSelection.operation.id`,

    handleSideDrawerOpen: signal`SideDrawer.handleDrawerOpen`,
    dataTabsToggle:       signal`DataTabs.toggleTab`,
	},
	withStyles(styles, { withTheme: true })(SideDrawer)
);


