import stateTree from './stateTree.js';
import { Module } from 'cerebral';

import {
  doneMovingMap,
  handleLocationFound,
  handleMapClick,
  handleMapMoved,
  startMovingMap,
  startMarkerDrag,
  endMarkerDrag,
  markerDragging,
  deleteCoordinate,
} from './chains';

export default Module({
	state: stateTree,

	signals: {
        locationFound: handleLocationFound,
        mapMoved: [
            ...handleMapMoved, ...doneMovingMap,
        ],
        mapMoveStarted: startMovingMap,
        mouseDownOnMap: handleMapClick,
        markerDragStarted: startMarkerDrag,
        markerDragEnded: endMarkerDrag,
        markerDragged: markerDragging,
        deleteCoordinate: deleteCoordinate,
    }
})
